.c-notification {
  position: fixed;
  z-index: 1000;
  padding: 1.25em; }
  .c-notification--toast {
    max-width: 20.625em;
    margin: 1.25em; }
    .c-notification--toast-top-left {
      top: 0;
      left: 0;
      transform: translateX(-21.875em);
      animation: slide-in-left 1s forwards, slide-out-left 1s forwards 5s; }
    .c-notification--toast-top-right {
      top: 0;
      right: 0;
      transform: translateX(21.875em);
      animation: slide-in-right 1s forwards, slide-out-right 1s forwards 5s; }
    .c-notification--toast-bottom-right {
      right: 0;
      bottom: 0;
      transform: translateX(21.875em);
      animation: slide-in-right 1s forwards, slide-out-right 1s forwards 5s; }
    .c-notification--toast-bottom-left {
      bottom: 0;
      left: 0;
      transform: translateX(-21.875em);
      animation: slide-in-left 1s forwards, slide-out-left 1s forwards 5s; }
  .c-notification--notice {
    color: #7997a1;
    background-color: #ADD8E6;
    border: 1px solid #9cc2cf; }
  .c-notification--warning {
    color: #a8a162;
    background-color: #F0E68C;
    border: 1px solid #d8cf7e; }
  .c-notification--error {
    color: #904040;
    background-color: #CD5C5C;
    border: 1px solid #b95353; }
  .c-notification--success {
    color: #479077;
    background-color: #66CDAA;
    border: 1px solid #5cb999; }
  .c-notification--top-bar {
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    border-top: 0;
    border-right: 0;
    border-left: 0;
    transform: translateY(-100%);
    animation: slide-down 1s forwards, slide-up 1s forwards 5s; }

